import { geojsonToLayer } from './vector-loader.js';
import { JobController } from './job-controller.js';
import { showBusy, hideBusy } from './ui-utils.js';
// 重い依存は初期バンドルから外す（必要時にだけ読み込む）
const lazy = {
  geotiff: () => import('./geotiff-loader.js'),
  projUI:  () => import('./projection-manager.js'),
  shp:     () => import('shpjs'),
  fflate:  () => import('fflate'),
};

// ====== P1 実配線：GeoTIFF / SHP の入力ハンドラを JobController で包む ======
const jobControllers = { raster: new JobController('raster'), vector: new JobController('vector') };
let currentLayers = { raster: { layer: null, revoke: null }, vector: { layer: null } };

// === 共通レンダラ（基本は map.options.renderer、無ければ SVG） ===
// 未設定ならCanvas（vecPane）で描画
function getPreferredRenderer(map) {
  if (map?.options?.renderer) return map.options.renderer;
  return L.canvas({ padding: 0.5, pane: 'vecPane' });
}

// ====== パッチA：エンコーディング判定とフィールド名正規化 ======
// （上部のユーティリティ付近に置く） -------------------------
function resolveDbfEncodingFromCpg(cpgText) {
  if (!cpgText) return null;
  const s = cpgText.trim().toUpperCase();
  // 代表的な表記を shapefile.js の TextDecoder ラベルに寄せる
  if (s.includes('CP932') || s.includes('SHIFT_JIS') || s.includes('SJIS')) return 'shift_jis';
  if (s.includes('EUC-JP') || s.includes('EUCJP')) return 'euc-jp';
  if (s.includes('UTF-8') || s.includes('UTF8') || s.includes('65001')) return 'utf-8';
  return null;
}

/**
 * フィールド名の正規化：
 * - 破損/非ASCIIの列名 → F01, F02... に付け替え
 * - props.__alias に {Fxx: "元の列名"} を残す（表示時に使う）
 * - キー重複も避ける
 */
function normalizeFieldNamesInFeatureCollection(fc) {
  let changed = 0;
  for (const f of (fc.features || [])) {
    const src = f.properties || {};
    const dst = {};
    const alias = {};
    let seq = 1;

    const used = new Set();

    for (const [k, v] of Object.entries(src)) {
      const bad = /[\uFFFD]/.test(k) || /[^\x20-\x7E]/.test(k); // 置換文字 or 非ASCII
      let key = k;

      if (bad || used.has(key)) {
        key = `F${String(seq++).padStart(2, '0')}`;
        alias[key] = k; // 表示用に元名を保持
        changed++;
      }
      // 念のためASCII以外は避ける（UIやインデクスの安定性確保）
      key = key.replace(/[^\x20-\x7E]/g, '_');
      while (used.has(key)) {
        key = `F${String(seq++).padStart(2, '0')}`;
        if (!alias[key]) alias[key] = k;
      }

      used.add(key);
      dst[key] = v;
    }

    if (Object.keys(alias).length) dst.__alias = alias;
    f.properties = dst;
  }
  return changed;
}

// --- 追加：見出しの表記ルール（プロジェクト固有の別名定義をここに） --------
// ここは“運用が楽”なように、必要最小限だけ埋めてください（データセット固有でもOK）
const FIELD_LABELS = {
  // 例：『壊れた列名 → 望ましい表示名』の対応を記録しておくと __alias が無い場合にも効く
  // 'xR[h': '班コード',
  // 'є': '小班',
  // 's': '市町村コード',
};

/**
 * 表示用テーブル：Fxx化されたキーでも、props.__aliasやFIELD_LABELSで人間可読に。
 * - props.__alias: { F01: '元の列名', ... }
 * - FIELD_LABELS: 固定の別名辞書（任意）
 */
function propsTableHTML(props, caption) {
  const alias = props.__alias || {};
  const rows = Object.entries(props)
    .filter(([k]) => k !== '__alias')
    .map(([k, v]) => {
      // 表示名の決定
      let label = alias[k] || FIELD_LABELS[k] || k;
      // 値を可読に
      const val = (v == null) ? '' : (typeof v === 'object' ? JSON.stringify(v) : String(v));
      return `<tr><th style="text-align:left;padding:2px 6px;color:#555">${label}</th><td style="padding:2px 6px">${val}</td></tr>`;
    })
    .join('');
  return (caption ? `<div style="font-weight:600;margin:.25rem 0">${caption}</div>` : '')
    + `<table style="font:12px system-ui;border-collapse:collapse">${rows}</table>`;
}

// 起動時（または初回読込時）に1回だけ（存在すれば）読み込む
(async () => {
  const tryJson = async (url) => {
    try {
      const r = await fetch(url, { cache: 'no-store' });
      return r.ok ? await r.json() : null;
    } catch { return null; }
  };
  const rel = await tryJson('config/field-labels.json');
  const abs = rel || await tryJson('/config/field-labels.json');
  if (abs) {
    Object.assign(FIELD_LABELS, abs);
    console.log('[FIELD_LABELS] loaded', Object.keys(FIELD_LABELS).length);
  }
})();

// 文字化けっぽいパターン（UTF-8を誤ってSJISで読んだ/逆も含む典型）
function looksMojibakeStr(s) {
  if (!s) return false;
  return /(?:Ã.|Â.|ã‚|ãƒ|ã)/.test(String(s));
}

function geojsonLooksMojibake(geo) {
  try {
    const feats = Array.isArray(geo?.features) ? geo.features : [];
    let total = 0, bad = 0;
    for (let i = 0; i < Math.min(feats.length, 60); i++) {
      const props = feats[i]?.properties || {};
      for (const k of Object.keys(props)) {
        const v = props[k];
        if (v == null || v === '') continue;
        total++;
        if (looksMojibakeStr(v)) bad++;
        if (total >= 30) break;
      }
      if (total >= 30) break;
    }
    return total > 0 && (bad / total) > 0.2;
  } catch { return false; }
}

function addVectorToMap(geojson) {
  const m = getLeafletMap();
  if (!m) { console.warn('[vector] map not ready'); return; }
  const renderer = getPreferredRenderer(m);       // ← Canvas を返す
  const style = (feat) => {                       // 線0.6px／ポリゴン塗りは上限0.06
    const isPoly = /Polygon/.test(feat?.geometry?.type || '');
    return isPoly
      ? { weight: 0.6, color: '#3388ff', fill: true,  fillOpacity: 0.06 }
      : { weight: 0.6, color: '#3388ff', fill: false };
  };
  const lyr = L.geoJSON(geojson, {
    pane: 'vecPane',                               // ← 前面の専用pane
    renderer,
    interactive: true,                             // ← クリック可
    bubblingMouseEvents: true,
    style,
  });
  lyr.addTo(m);
  // 前面へ（Canvasはpane順優先だが念のため）
  if (lyr.bringToFront) try { lyr.bringToFront(); } catch {}

  // 親レイヤ1本で委譲
  lyr.on('click', (e) => {
    const props = e.layer?.feature?.properties || {};
    L.popup().setLatLng(e.latlng)
      .setContent(propsTableHTML(props, '小班属性'))
      .openOn(m);
  });

  window.__vectorLayer = lyr;
  const cnt = lyr.getLayers().length;
  console.info('[vector] added features =', cnt);
  try {
    const b = lyr.getBounds();
    if (b?.isValid?.()) m.fitBounds(b, { maxZoom: 17 });
  } catch {}
}

async function loadShapefileZip(file) {
  const ab = await file.arrayBuffer();
  // ① まずは通常の parseZip（デフォルトUTF-8）で読む
  const { parseZip, parseShp, parseDbf, combine } = await lazy.shp();
  let geojson = null;
  try { geojson = await parseZip(ab); } catch (e) { console.warn(e); }

  // ② 文字化けなら、ZIPを自前展開 → .cpg 参照やヒューリスティックで再読込
  if (!geojson || geojsonLooksMojibake(geojson)) {
    const u8 = new Uint8Array(ab);
    const { unzipSync, strFromU8 } = await lazy.fflate();
    const zipped = unzipSync(u8);                 // { filename: Uint8Array }
    const names  = Object.keys(zipped);
    // 一番それっぽい .shp をひとつ選ぶ
    const shpName = names.find(n => n.toLowerCase().endsWith(".shp"));
    if (!shpName) throw new Error("SHP が見つかりません");
    const base = shpName.slice(0, shpName.lastIndexOf("."));
    const dbfName = names.find(n => n.toLowerCase().endsWith(".dbf") && n.slice(0, n.lastIndexOf(".")) === base)
                   || names.find(n => n.toLowerCase().endsWith(".dbf"));
    if (!dbfName) throw new Error("DBF が見つかりません");
    
    // --- パッチAの実装：.cpg 優先→判定、なければフォールバック順（EUC-JP→Shift_JIS→UTF-8） ---
    const cpgName = names.find(n => /\.cpg$/i.test(n));
    const cpgText = cpgName ? strFromU8(zipped[cpgName]) : null;

    // 1) .cpg 優先→判定、なければフォールバック順（EUC-JP→Shift_JIS→UTF-8）
    let encoding = resolveDbfEncodingFromCpg(cpgText);
    let gj = null, tried = [];
    async function readWith(enc) {
      tried.push(enc);
      try {
        if (!globalThis.shapefile?.read) return null; // ライブラリ未読込ならスキップ
        return await shapefile.read(shpName, dbfName, { encoding: enc });
      }
      catch (e) { console.warn('[SHP] read failed with', enc, e); return null; }
    }
    if (encoding) {
      gj = await readWith(encoding);
    }
    if (!gj) {
      for (const enc of ['euc-jp', 'shift_jis', 'utf-8']) {
        if (enc === encoding) continue;
        gj = await readWith(enc);
        if (gj) { encoding = enc; break; }
      }
    }
    if (!gj || !gj.features?.length) {
      console.error('[SHP] no features (enc tried =', tried, ')');
      return; // 既存のエラーハンドリングに合わせる
    }

    // 2) フィールド名の正規化（破損列名→Fxx、別名は props.__alias）
    const changed = normalizeFieldNamesInFeatureCollection(gj);
    console.log('[SHP] encoding:', encoding, ' aliasized fields:', changed);
    
    geojson = gj;
    console.info('[SHP] reloaded with encoding');
  }
  if (!geojson || !geojson.features?.length) { console.warn('[SHP] no features'); return; }
  addVectorToMap(geojson);
}

// === 追加: ベクタ属性ポップアップ用ユーティリティ ===
let __selectedFeatureLayer = null;

function highlight(layer) {
  try {
    if (__selectedFeatureLayer && __selectedFeatureLayer.setStyle) {
      // どこから呼んでも上限を超えないように共通関数で丸める
      const THIN = 0.6, FILL_CAP = 0.06;
      const clamp = (style, isPoly) => ({
        weight: Math.min(Number(style.weight ?? THIN), THIN),
        fill: !!isPoly,
        fillOpacity: isPoly ? Math.min(Number(style.fillOpacity ?? FILL_CAP), FILL_CAP) : 0
      });
      const isPoly = __selectedFeatureLayer instanceof L.Polygon;
      __selectedFeatureLayer.setStyle(clamp({ fillOpacity: 1.5, weight: 3 }, isPoly)); // reset
    }
    if (layer && layer.setStyle) {
      // どこから呼んでも上限を超えないように共通関数で丸める
      const THIN = 0.6, FILL_CAP = 0.06;
      const clamp = (style, isPoly) => ({
        weight: Math.min(Number(style.weight ?? THIN), THIN),
        fill: !!isPoly,
        fillOpacity: isPoly ? Math.min(Number(style.fillOpacity ?? FILL_CAP), FILL_CAP) : 0
      });
      const isPoly = layer instanceof L.Polygon;
      layer.setStyle(clamp({ fillOpacity: 3, weight: 3 }, isPoly)); // highlight
      __selectedFeatureLayer = layer;
    }
  } catch {}
}

function getMapOrThrow() {
  const map = window.leafletMap || window.map || null;
  if (!map) throw new Error('Map is not initialized yet.');
  return map;
}

// ---- P1 配線有効フラグ（旧ハンドラの暴発を完全に止める）
const USE_P1_PIPELINE = true;

export function wireP1HandlersOnce() {
  // 既存要素に旧 addEventListener が付いている可能性が高いので
  // 一度 cloneNode(true) して置換 → 旧リスナを物理的に除去してから新リスナを付ける
  const findAndClone = (el) => {
    if (!el) return null;
    const cloned = el.cloneNode(true);
    el.parentNode && el.parentNode.replaceChild(cloned, el);
    return cloned;
  };

  let gi =
    document.getElementById('geotiffInput') ||
    document.querySelector('[data-role="geotiff-input"]') ||
    document.querySelector('input[type="file"][accept*="tif"]');
  let si =
    document.getElementById('shpZipInput') ||
    document.querySelector('[data-role="shpzip-input"]') ||
    document.querySelector('input[type="file"][accept*="zip"]');

  // 旧ハンドラを除去
  gi = findAndClone(gi);
  si = findAndClone(si);

  // 念のため data-legacy-run を見て旧コード側の分岐があれば強制停止
  if (gi) gi.setAttribute('data-stop-legacy', '1');
  if (si) si.setAttribute('data-stop-legacy', '1');

  if (gi && !gi.dataset.p1Wired) {
    gi.dataset.p1Wired = '1';
    gi.addEventListener('change', async (ev) => {
      if (!USE_P1_PIPELINE) return;
      const file = ev.target.files?.[0];
      if (!file) return;
      await jobControllers.raster.run('raster-load', async (signal) => {
        const map = getMapOrThrow();
        showBusy('GeoTIFF を読み込み中…');
        // 読み込み
        const buf = await file.arrayBuffer();
        // 必要なら EPSG:32767 を別名付け（静かに失敗可）
        aliasEPSG32767To('EPSG:32654');
        const { loadGeoTiffAsOverlay } = await lazy.geotiff();
        const res = await loadGeoTiffAsOverlay(buf, { signal });
        if (signal.aborted) return;
        if (!res.ok) {
          if (res.reason === 'NEED_CRS') {
            // UIで 4326/3857/TWF を選んでもらう
            const { ensureRasterCRSUI } = await lazy.projUI();
            const choice = await ensureRasterCRSUI({ suggestion: res.suggestion });
            if (!choice) { hideBusy(); return; } // キャンセル → 消灯
            const { loadGeoTiffAsOverlay } = await lazy.geotiff();
            const res2 = await loadGeoTiffAsOverlay(buf, { signal, forcedCrs: choice.crs, worldFile: choice.tfw });
            if (!res2.ok) throw new Error(res2.reason || 'Failed to load GeoTIFF');
            return finalizeRaster(map, res2);
          }
          hideBusy(); // 失敗時は消灯
          throw new Error(res.reason || 'Failed to load GeoTIFF');
        }
        return finalizeRaster(map, res);
      });
    });
  }

  if (si && !si.dataset.p1Wired) {
    si.dataset.p1Wired = '1';
    si.addEventListener('change', async (ev) => {
      if (!USE_P1_PIPELINE) return;
      const file = ev.target.files?.[0];
      if (!file) return;
      await jobControllers.vector.run('vector-load', async (signal) => {
        const map = getMapOrThrow();
        showBusy('Shapefile を読み込み中…');
        try {
          await loadShapefileZip(file);
        } finally {
          hideBusy(); // ベクタは addTo後ほぼ即表示なのでここで消灯
        }
      });
    });
  }
}

function finalizeRaster(map, loadResult) {
  // 旧レイヤ削除＆URL解放
  if (currentLayers.raster.layer) {
    try { map.removeLayer(currentLayers.raster.layer); } catch {}
  }
  if (currentLayers.raster.revoke) {
    try { currentLayers.raster.revoke(); } catch {}
  }
  currentLayers.raster = { layer: loadResult.layer, revoke: loadResult.revoke };
  const m = getLeafletMap();
  if (m) loadResult.layer.addTo(m);
  if (m) {
    // GeoTIFFは“画像ロード完了”で消灯（タイムアウト無し）
    loadResult.layer.once?.('load', () => hideBusy());
    m.fitBounds(loadResult.bounds, { maxZoom: 17 });
  }
}

function finalizeVector(map, geojson) {
  const m = getLeafletMap();
  if (!m) return;
  if (currentLayers.vector.layer) {
    try { m.removeLayer(currentLayers.vector.layer); } catch {}
  }

  const renderer = getPreferredRenderer(m);
  const lyr = L.geoJSON(geojson, {
    pane: 'overlayPane',
    renderer,
    interactive: true,
    style: { weight: 1, color: '#3388ff', fillOpacity: 0.2 },
  });
  lyr.addTo(m);

  lyr.on('click', (e) => {
    const props = e.layer?.feature?.properties || {};
    L.popup().setLatLng(e.latlng)
      .setContent(propsTableHTML(props, '小班属性'))
      .openOn(m);
  });

  currentLayers.vector.layer = lyr;
  try {
    const b = lyr.getBounds();
    if (b?.isValid?.()) m.fitBounds(b, { maxZoom: 17 });
  } catch {}
}

// ---- Leaflet Map resolver（DOMの window.map 汚染を回避）----
function getLeafletMap() {
  const m = globalThis.__leaflet_map__;
  if (m && typeof m.addLayer === 'function') return m;
  // 最後の保険：window.map が“たまたま”本物なら使う（通常は DOM 要素なので弾かれる）
  const wm = globalThis.map;
  if (wm && typeof wm.addLayer === 'function') return wm;
  return null;
}

function wireShpZip(){
  const inp = document.getElementById('shpZipInput');
  if (!inp) { console.warn('[SHP] #shpZipInput が見つかりません'); return; }
  if (!inp.__wired) {
    inp.addEventListener('change', async () => {
      const f = inp.files?.[0];
      console.info('[SHP] file change:', f?.name, f?.size);
      if (f) await loadShapefileZip(f);
    });
    inp.__wired = true;
  }
  // もし「ボタン → input.click()」方式なら、ボタン側にも保険を掛ける
  const btn = document.querySelector('[data-action="pick-shpzip"], #btnShpZip, #loadShpZip');
  if (btn && !btn.__wired) {
    btn.addEventListener('click', ()=> inp.click());
    btn.__wired = true;
  }
  console.info('[SHP] wired');
}

document.addEventListener('DOMContentLoaded', () => {
  if (!USE_P1_PIPELINE) { try { wireShpZip(); } catch {} }
});

// GeoTIFF: EPSG:32767 → 任意EPSGへ別名（既定=EPSG:32654）
function aliasEPSG32767To(code='EPSG:32654'){
  try {
    if (globalThis.proj4?.defs) {
      const base = proj4.defs(code);
      if (base && !proj4.defs('EPSG:32767')) { proj4.defs('EPSG:32767', base); return true; }
    }
  } catch {}
  return false;
}
