import L from 'leaflet';
import { registerOverlay } from './opacity-control.js';

// オフラインモードの確認
const isOffline = typeof __VITE_IS_OFFLINE__ !== 'undefined' ? __VITE_IS_OFFLINE__ : false;

export function initMap() {
  // Leafletのマーカー画像パスを設定（ローカル配信用）
  L.Icon.Default.imagePath = './images/';

  // マップ初期化 + 参照公開（他処理からの互換参照も提供）
  const existing = globalThis.__leaflet_map__;
  const map = existing || L.map('map', {
    center: [43.06417, 141.34694],
    zoom: 9,
    preferCanvas: true,
  });
  // ベクタ専用pane（マーカー600 < ここ670 < ポップアップ700）
  if (!map.getPane('vecPane')) {
    const pane = map.createPane('vecPane');
    pane.style.zIndex = '670';
    pane.style.pointerEvents = 'auto';
  }
  // 既定レンダラをCanvasへ（初回からCanvas描画）
  map.options.renderer = L.canvas({ padding: 0.5, pane: 'vecPane' });
  map.options.preferCanvas = true;
  map.on('click.__probe', e => console.log('[MAP click]', e.latlng));
  console.log('[map-init] renderer =', map.options.renderer instanceof L.SVG ? 'SVG' : 'Canvas');
  if (!existing) {
    globalThis.__leaflet_map__ = map;
  }
  
  // 他のモジュール/関数から確実に参照できるよう、Map を公開
  window._mapRef = map;
  window.leafletMap = map; // 旧コード互換用

  // OSM ベース（透過操作の対象外に固定）
  const base = L.tileLayer('https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png', {
    maxZoom: 19,
    attribution: '&copy; OpenStreetMap contributors',
    opacity: 1,
    pane: 'tilePane'
  });
  
  const m = getLeafletMap();
  if (m) base.addTo(m);

  // 互換性のためグローバルへも保持しておく
  try { window.__leaflet_map__ = map; } catch {}
  
  // プロジェクト共通の“クランプ”フック（ぶり返し防止）
  if (!L.Path.prototype.setStyle.__clamped) {
    const ORG = L.Path.prototype.setStyle;
    const THIN = 0.6, FILL_CAP = 0.06;
    L.Path.prototype.setStyle = function(style){
      const feat = this.feature || (typeof this.toGeoJSON==='function' ? (this.toGeoJSON().features ? null : this.toGeoJSON()) : null);
      const isPoly = /Polygon/.test(feat?.geometry?.type || '') || (typeof L.Polygon!=='undefined' && this instanceof L.Polygon);
      const s = { ...(style||{}) };
      const w = Number(s.weight);
      s.weight = (isFinite(w) && w>0) ? Math.min(w, THIN) : THIN;
      if (isPoly) { s.fill = true; s.fillOpacity = Math.min(Number(s.fillOpacity ?? FILL_CAP), FILL_CAP); }
      else { s.fill = false; }
      return ORG.call(this, s);
    };
    L.Path.prototype.setStyle.__clamped = true;
  }
  
  // ★ 必ず返す
  return map;

  // ============ A) オーバーレイ管理（OSMは登録しない） ============
  /** @type {Map<number, L.Layer>} */
  const overlayRegistry = new Map();

  // ============ D) オーバーレイ追加の共通フック ============
  function onOverlayAdded(layer, name) {
    if (!layer) return;
    if (layer?.options?.pane === 'tilePane') return; // ベース除外
    // 透過スライダーやレイヤリストへの行追加は行いません（撤去済み）
  }

  // ============ E) onReady 安全呼び出し（未定義/誤用の無害化） ============
  function safeInvoke(fn, ...args) {
    if (typeof fn === 'function') {
      try { return fn(...args); } catch (e) { console.error(e); }
    }
  }

  // ========================
  // F) サンプルデータの追加
  // ========================
  // （例）GeoTIFF：未定義でも落ちない多段ガード（情報ログのみ）
  return;
  
  /*
  const _hasUrlOrBlob = (typeof urlOrBlob !== 'undefined' && urlOrBlob);
  const _hasBounds     = (typeof bounds     !== 'undefined' && bounds);
  const _tiffSrc   = _hasUrlOrBlob ? urlOrBlob
               : (window.TIFF_SRC || document.querySelector('[data-tiff-src]')?.dataset.tiffSrc);
  const _tiffBnds  = _hasBounds ? bounds
               : (window.TIFF_BOUNDS || null);
  if (_tiffSrc && _tiffBnds) {
    const tiffOverlay = L.imageOverlay(_tiffSrc, _tiffBnds);
    const m = getLeafletMap();
    if (m) tiffOverlay.addTo(m);
    registerOverlay(tiffOverlay, 'GeoTIFF', 1);
    // onOverlayAdded(tiffOverlay, 'GeoTIFF');
  } else {
    if (document.querySelector('#geotiffInput')?.dataset['stop-legacy'] === '1') return;
    console.warn('GeoTIFF の src/bounds が未定義のため追加をスキップしました。');
  }
  */
  // （例）SHP/GeoJSON：未定義でも落ちない多段ガード
  const _hasGeoJSON = (typeof geojson !== 'undefined' && geojson);
  const _gjData = _hasGeoJSON ? geojson : (window.GEOJSON_DATA || null);
  if (_gjData) {
    const parcel = L.geoJSON(_gjData, (typeof style !== 'undefined' ? style : undefined));
    const m = getLeafletMap();
    if (m) parcel.addTo(m);
    registerOverlay(parcel, '林小班区画', 1);
    // onOverlayAdded(parcel, '林小班区画');
    safeInvoke(onReady, parcel);   // onReady 未定義でも例外化しない
  } else {
    if (document.querySelector('#shpZipInput')?.dataset['stop-legacy'] === '1') return;
    console.warn('geojson が未定義のため追加をスキップしました。');
  }

  // ① 既存コードの後ろに追加（DOMContentLoaded後でOK）
  ;(function(){
    // 「1.範囲選択 / 2. 重ね合わせ / 3. 出力」系のボタン群を安全に除去
    const killTexts = ["1.範囲選択","1.範囲選択","2. 重ね合わせ","2.重ね合わせ","3. 出力","3.出力"];
    document.querySelectorAll('button, .btn, .leaflet-control button').forEach(el=>{
      if(killTexts.some(t => (el.textContent||"").trim()===t)) el.remove();
    });
    // 空になったグループコンテナがあれば隠す
    document.querySelectorAll('.btn-group,.stepbar,.steps').forEach(g=>{
      if(!g.querySelector('button')) g.remove();
    });

    // ② スライダーの数値出力
    const r = document.getElementById('opacityRaster');
    const v = document.getElementById('opacityVector');
    const or = document.getElementById('outRaster');
    const ov = document.getElementById('outVector');
    if(r&&or){ r.addEventListener('input',()=> or.textContent = (+r.value).toFixed(2)); }
    if(v&&ov){ v.addEventListener('input',()=> ov.textContent = (+v.value).toFixed(2)); }

    // ③ もし既存の不透明度反映関数があるならそのまま呼ぶ（例）
    // r?.addEventListener('input', e => setRasterOpacity(+e.target.value));
    // v?.addEventListener('input', e => setVectorOpacity(+e.target.value));

    // ④ ステータス表示（既存の座標・縮尺イベントに統合推奨）
    const stScale = document.getElementById('st-scale');
    const stCRS   = document.getElementById('st-crs');
    const stXY    = document.getElementById('st-xy');
    if(window.map && stScale){
      leafletMap.on('zoomend',()=>{ 
        try{ 
          // Get zoom level as a proxy for scale since Leaflet doesn't have getScale()
          stScale.textContent = '1:' + Math.round(Math.pow(2, leafletMap.getZoom()));
        }catch{} 
      });
      leafletMap.on('mousemove',e=>{ 
        stXY.textContent = `X: ${e.latlng.lng.toFixed(5)} , Y: ${e.latlng.lat.toFixed(5)}`; 
      });
      try{ 
        // Get CRS information if available
        stCRS.textContent = 'CRS: ' + (leafletMap.options.crs?.code || '—'); 
      }catch{}
    }
  })();

  // Leaflet準備を待つ
  function onReady(cb) {
    if (window.L) return cb();
    window.addEventListener('load', () => cb(), { once: true });
  }
}

// ---- Leaflet Map resolver（DOMの window.map 汚染を回避）----
function getLeafletMap() {
  const m = globalThis.__leaflet_map__;
  if (m && typeof m.addLayer === 'function') return m;
  // 最後の保険：window.map が“たまたま”本物なら使う（通常は DOM 要素なので弾かれる）
  const wm = globalThis.map;
  if (wm && typeof wm.addLayer === 'function') return wm;
  return null;
}