import { report } from './errors.js';
import L from 'leaflet';

// === Diagnostics パネル（?diag=1） ===
export function renderDiagnostics() {
  // === クエリパラメータ ===
  const __params = new URLSearchParams(location.search);
  const __wantDiag = __params.get('diag') === '1';
  
  if (!__wantDiag) return;
  const box = document.createElement('div');
  box.style.cssText = 'position:fixed;right:12px;top:12px;z-index:2000;background:#fff;padding:10px 12px;border-radius:10px;box-shadow:0 6px 18px rgba(0,0,0,.12);font:12px/1.5 system-ui';
  box.innerHTML = '<div style="font-weight:700;margin-bottom:6px">Diagnostics</div><div id="diagBody"></div>';
  document.body.appendChild(box);
  const items = [];
  const push = (name, ok, note='') => items.push({ name, ok, note });

  // 1) JSバンドル
  push('App module loaded', true);

  // 2) Leaflet import
  push('Leaflet import', !!L?.map, L?.version ? 'v'+L.version : '');

  // 3) Canvas API
  const cv = document.createElement('canvas');
  push('Canvas 2D', !!cv.getContext?.('2d'));

  // 4) createImageBitmap
  push('createImageBitmap', 'createImageBitmap' in window);

  // 5) Online/Offline
  push('navigator.onLine', navigator.onLine);

  // 6) CSS（leaflet.css の適用痕跡）
  const probe = document.createElement('div');
  probe.className = 'leaflet-pane';
  document.body.appendChild(probe);
  const st = getComputedStyle(probe);
  const cssOk = st.position === 'absolute';
  document.body.removeChild(probe);
  push('Leaflet CSS', cssOk);

  const body = box.querySelector('#diagBody');
  body.innerHTML = items.map(it => {
    const color = it.ok ? '#0a0' : '#c00';
    const note  = it.note ? ` <span style="color:#666">${it.note}</span>` : '';
    return `<div><span style="display:inline-block;width:12px;height:12px;border-radius:50%;background:${color};margin-right:6px;vertical-align:-2px"></span>${it.name}${note}</div>`;
  }).join('');
}