export const ERR = {
  GEO_OPEN: 'GEO-100',
  GEO_PARSE: 'GEO-110',
  GEO_GET_IMAGE: 'GEO-120',
  GEO_PICK_OV: 'GEO-130',
  GEO_RASTERS: 'GEO-140',
  GEO_RENDER: 'GEO-150',
  MAP_ADD: 'MAP-100',
  SET_SAVE: 'SET-100',
};

export const DEBUG = {
  on: true, // 本番で消したい場合は false
};

export function raise(code, message, ctx = {}, cause) {
  const e = new Error(message);
  e.code = code;
  e.ctx = ctx;
  e.cause = cause;
  return e;
}

// 共通ロガー
export function report(payload, level = 'info') {
  if (!DEBUG.on) return;
  const ts = new Date().toLocaleTimeString();
  const base = `[%c${ts}%c]`;
  const styles = ['color:#888;font-weight:bold', 'color:inherit'];
  if (payload instanceof Error) {
    console.groupCollapsed(`${base} %cERROR%c ${payload.code || ''} ${payload.message}`, ...styles, 'color:#c00;font-weight:bold', 'color:inherit');
    if (payload.ctx) console.log('ctx:', payload.ctx);
    if (payload.cause) console.error('cause:', payload.cause);
    console.groupEnd();
    return;
  }
  if (level === 'warn') {
    console.log(`${base} %cWARN%c`, ...styles, 'color:#b58900;font-weight:bold', 'color:inherit', payload);
  } else if (level === 'ok') {
    console.log(`${base} %cOK%c`, ...styles, 'color:#0a0;font-weight:bold', 'color:inherit', payload);
  } else {
    console.log(base + ' %cINFO%c', ...styles, 'color:#06c;font-weight:bold', 'color:inherit', payload);
  }
}

// 区切り付きステップロガー
export async function step(label, fn) {
  const t0 = performance.now();
  const ts = new Date().toLocaleTimeString();
  console.log(`[%c${ts}%c] %cSTART%c ${label}`, 'color:#888;font-weight:bold', 'color:inherit', 'color:#333;background:#ffd;padding:0 4px;border-radius:3px', 'color:inherit');
  try {
    const res = await fn();
    const dt = (performance.now() - t0).toFixed(1);
    console.log(`[%c${ts}%c] %cOK%c    ${label} (${dt} ms)`, 'color:#888;font-weight:bold', 'color:inherit', 'color:#0a0;font-weight:bold', 'color:inherit');
    return res;
  } catch (e) {
    const dt = (performance.now() - t0).toFixed(1);
    console.error(`[%c${ts}%c] %cFAIL%c  ${label} (${dt} ms)`, 'color:#888;font-weight:bold', 'color:inherit', 'color:#c00;font-weight:bold', 'color:inherit');
    report(e);
    throw e;
  }
}

// 画面左下に簡易トレイを出す（?debug=1 で有効化）
export function attachDebugTrayIfRequested() {
  if (!/[?&]debug=1/.test(location.search)) return;
  const tray = document.createElement('div');
  Object.assign(tray.style, {
    position:'fixed', left:'8px', bottom:'8px', maxHeight:'40vh',
    overflow:'auto', background:'rgba(0,0,0,.75)', color:'#fff',
    font:'12px/1.4 monospace', padding:'6px 8px', borderRadius:'8px',
    zIndex: 9999
  });
  document.body.appendChild(tray);
  const orig = report;
  // report を薄くラップしてUIにも複製
  window.report = (payload, level='info') => {
    orig(payload, level);
    const msg = payload instanceof Error ? (payload.code || '') + ' ' + payload.message :
      (typeof payload === 'string' ? payload : JSON.stringify(payload));
    const line = document.createElement('div');
    line.textContent = `${new Date().toLocaleTimeString()} [${level}] ${msg}`;
    tray.prepend(line);
  };
}

export function statsOf(arr) {
  let min = Infinity, max = -Infinity, nan = 0;
  for (let i = 0; i < arr.length; i++) {
    const v = arr[i];
    if (!Number.isFinite(v)) { nan++; continue; }
    if (v < min) min = v;
    if (v > max) max = v;
  }
  return { length: arr.length, min, max, nan, nanRatio: +(nan / arr.length).toFixed(6) };
}