// --- Layersカードと「透明度」ホストを用意（UIのみ・機能不変） ---
export function ensureLayersHost() {
  const findSec = (kw) => {
    const spans = Array.from(document.querySelectorAll('aside.side .card-h .card-t, aside.side .card-h'));
    const hit = spans.find(s => (s.textContent||'').includes(kw));
    return hit ? hit.closest('section.card') : null;
  };
  const loadSec = findSec('レイヤ') || findSec('レイヤー読み込み');
  if (!loadSec) return null;
  let card = document.getElementById('layersCard');
  if (!card) {
    card = document.createElement('section'); card.className='card'; card.id='layersCard';
    const head=document.createElement('header'); head.className='card-h';
    const title=document.createElement('span'); title.className='card-t'; title.textContent='Layers';
    const body=document.createElement('div'); body.className='card-c';
    head.appendChild(title); card.append(head, body);
    loadSec.after(card);
  }
  let grp = document.getElementById('opacityGroup');
  if (!grp) {
    grp = document.createElement('div'); grp.id='opacityGroup';
    const h=document.createElement('div'); h.className='op-h'; h.textContent='透明度';
    const b=document.createElement('div'); b.className='op-body';
    grp.append(h,b);
    card.querySelector('.card-c').prepend(grp);
  }
  return grp.querySelector('.op-body');
}

// --- 再発防止の軽量ガード（左上に出た #layerList/opacity-slider を即座に右へ集約） ---
export function armLayersGuard() {
  if (window.__layersGuardObs) return; // 二重起動防止
  const host = ensureLayersHost(); if (!host) return;
  const move = (el) => {
    if (!(el instanceof HTMLElement)) return false;
    if (host.contains(el) || el.contains(host)) return false; // 親子ループ回避
    el.classList?.remove('leaflet-bar');
    el.style.float='none'; el.style.margin='0'; el.style.width='100%';
    host.appendChild(el);
    return true;
  };
  // 起動時に既存を寄せる
  ['#layerList', '.layer-row', 'input.opacity-slider', '#layer-panel'].forEach(sel => {
    document.querySelectorAll(sel).forEach(el => move(el));
  });
  // 以後の追加を監視して自動移設
  const obs = new MutationObserver(muts => {
    muts.forEach(m => (m.addedNodes||[]).forEach(n => {
      if (!(n instanceof HTMLElement)) return;
      ['#layerList', '.layer-row', 'input.opacity-slider', '#layer-panel'].forEach(sel => {
        if (n.matches?.(sel)) move(n);
        n.querySelectorAll?.(sel)?.forEach(el => move(el));
      });
    }));
  });
  obs.observe(document.body, { childList:true, subtree:true });
  window.__layersGuardObs = obs;
}

