// ===== ショートカット =====
export function initKeyboardShortcuts() {
  document.addEventListener('keydown', (e)=>{
    if(e.key==='?' || (e.shiftKey && e.key==='/')){ $('#hotkeyHelp').hidden=false; setTimeout(()=>$('#hotkeyHelp').hidden=true, 3000); }
    if(e.key==='o' || e.key==='O'){ e.preventDefault(); $('#geotiffInput')?.click(); }
  });
}

// ===== ファイル選択バー（自動挿入） =====
export function ensureFileBar() {
  let bar = document.getElementById('filebar');
  if (!bar) {
    bar = document.createElement('div');
    bar.id = 'filebar';
    bar.className = 'filebar';
    bar.innerHTML = `
      <button id="btn-choose" type="button" aria-label="ファイル選択">ファイル選択</button>
      <input id="file-input" type="file" multiple
        accept=".zip,.geojson,.json,.tif,.tiff"
        style="position:absolute;left:-9999px;top:-9999px;">
    `;
    document.body.appendChild(bar);
    // 最小CSS（パネルに覆われてクリックできない事故を防ぐ）
    if (!document.getElementById('filebar-inline-style')) {
      const st = document.createElement('style');
      st.id = 'filebar-inline-style';
      st.textContent = `
        .filebar{position:absolute;z-index:2000;top:10px;right:10px;background:rgba(255,255,255,.95);
          border-radius:8px;padding:8px 10px;box-shadow:0 2px 8px rgba(0,0,0,.15);display:flex;gap:8px}
        .filebar button{padding:6px 10px;border-radius:6px;border:1px solid #ddd;background:#fff;cursor:pointer}
      `;
      document.head.appendChild(st);
    }
  }
  const btn = bar.querySelector('#btn-choose');
  const fi  = bar.querySelector('#file-input');
  btn.onclick = () => fi.click(); // ユーザー操作起点なのでダイアログが開ける
  fi.onchange  = (ev) => { if (ev.target.files) handleFiles(ev.target.files); };
  // どこにドロップしても読める簡易DnD
  window.addEventListener('dragover', e => { e.preventDefault(); }, { passive:false });
  window.addEventListener('drop', e => {
    e.preventDefault();
    if (e.dataTransfer?.files?.length) handleFiles(e.dataTransfer.files);
  }, { passive:false });
}

function handleFiles(fileList) {
  for (const f of fileList) {
    // 互換保持：ファイル名→File を引けるよう記録（既存コードが参照）
    recentFiles.set(f.name, f);
    // 直接 File を渡す（文字列経由より堅牢）
    onLayerLoaded(f);
  }
}

