import L from 'leaflet';

export function installLocateControl() {
  const map =
    globalThis.__leaflet_map__ ||
    (window.L && Object.values(window).find(v => v instanceof L.Map));
  if (!map) { console.warn('[locate] map not found'); return; }

  let posMarker = null, accCircle = null;
  const draw = (lat, lng, acc) => {
    const ll = [lat, lng];
    if (!posMarker) posMarker = L.circleMarker(ll, { radius:7, weight:2 }).addTo(map);
    else posMarker.setLatLng(ll);
    if (!accCircle) accCircle = L.circle(ll, { radius: acc, weight:1, fillOpacity:0.1 }).addTo(map);
    else { accCircle.setLatLng(ll); accCircle.setRadius(acc); }
    map.setView(ll, Math.max(15, map.getZoom()));
  };

  const locate = () => {
    if (!navigator.geolocation) { alert('この端末では現在地取得に対応していません'); return; }
    navigator.geolocation.getCurrentPosition(
      pos => draw(pos.coords.latitude, pos.coords.longitude, pos.coords.accuracy),
      err => alert('現在地を取得できませんでした: ' + err.message),
      { enableHighAccuracy:true, timeout:10000, maximumAge:15000 }
    );
  };

  const ctrl = L.control({ position:'topleft' });
  ctrl.onAdd = () => {
    const wrap = L.DomUtil.create('div', 'leaflet-bar');
    const a = L.DomUtil.create('a', '', wrap);
    a.href = '#';
    a.title = '現在位置を表示';
    a.setAttribute('aria-label','現在位置を表示');
    a.textContent = '📍';
    L.DomEvent.on(a, 'click', (e)=>{ L.DomEvent.stop(e); locate(); });
    return wrap;
  };
  ctrl.addTo(map);
}