import L from 'leaflet';

// ====== Overlay registry (singleton) ======
const overlayRegistry = (globalThis.overlayRegistry ??= new Map()); // id -> { layer, name }

// ---------- utils: Leafletレイヤー判定と安全なID採番 ----------
function isLeafletLayer(x) {
  return !!(
    x &&
    typeof x === 'object' && (
      typeof x.addTo === 'function' ||
      typeof x.getPane === 'function' ||
      typeof x.remove === 'function' ||
      x._leaflet_id != null
    )
  );
}

function coerceLeafletLayer(x) {
  if (isLeafletLayer(x)) return x;
  if (x && typeof x === 'object' && isLeafletLayer(x.layer)) return x.layer; // {layer, name, opacity} 互換
  return null;
}

function safeStamp(layer) {
  try { return L.Util.stamp(layer); } catch (e) { return null; }
}

// 既定の SVG レンダラを返す（mapに既に設定があればそれを使う）
function getRendererForMap() {
  const m = getLeafletMap();
  return (m?.options?.renderer) || L.svg({ padding: 0.5 });
}

// ====== Apply opacity to any Leaflet-ish layer ======
function applyOpacity(layer, v) {
  if (!isLeafletLayer(layer) || Number.isNaN(v)) return;
  // 1) L.ImageOverlay / L.TileLayer / L.GridLayer 系
  if (typeof layer.setOpacity === 'function') {
    layer.setOpacity(v);
    return;
  }
  // 2) L.Path/L.GeoJSON（ベクタ）
  if (typeof layer.setStyle === 'function') {
    // 既存 style を壊さないよう必要最小限だけ
    layer.setStyle({ opacity: v, fillOpacity: v });
    // LayerGroup の場合は子にも波及させる
    if (typeof layer.eachLayer === 'function') {
      layer.eachLayer((ch) => applyOpacity(ch, v));
    }
    return;
  }
  // 3) DOM コンテナに直接（Canvas/WebGL/独自プラグイン等の保険）
  const el = (typeof layer.getElement === 'function') ? layer.getElement()
           : (layer._container ?? layer._mapPane ?? null);
  if (el && el.style) {
    el.style.opacity = String(v);
    return;
  }
  // 4) LayerGroup なら子へ
  if (typeof layer.eachLayer === 'function') {
    layer.eachLayer((ch) => applyOpacity(ch, v));
  }
}

// ====== UI row factory ======
function createOpacityRow(name, id, initial = 1) {
  const li = document.createElement('li');
  li.className = 'layer-row';
  li.innerHTML = `
    <span class="layer-name" title="${name}">${name}</span>
    <input type="range" class="opacity-slider" min="0" max="1" step="0.01"
           value="${initial}" data-layer-id="${id}" aria-label="${name} の不透明度">
  `;
  return li;
}

// ====== Wire UI (event delegation) ======
function wireOpacityUIOnce() {
  if (wireOpacityUIOnce._done) return; wireOpacityUIOnce._done = true;
  const listEl = ensureOpacityPanel();
  if (!listEl) return;
  listEl.addEventListener('input', (ev) => {
    const t = ev.target;
    if (!(t instanceof HTMLInputElement) || t.type !== 'range') return;
    const id = Number(t.dataset.layerId);
    const rec = overlayRegistry.get(id);
    if (!rec) return;
    const v = (t.max === '100') ? Number(t.value) / 100 : Number(t.value);
    applyOpacity(rec.layer, v);
  }, { passive: true });
}

// ====== Register a new overlay (call this when you add overlays) ======
export function registerOverlay(candidate, name = 'Overlay', initialOpacity = 1) {
  wireOpacityUIOnce(); // パネル無ければ作成
  const layer = coerceLeafletLayer(candidate);
  if (!layer) { console.warn('[opacity] registerOverlay: non-layer skipped', candidate); return; }
  const id = safeStamp(layer) ?? (Date.now() | 0) + Math.random();
  overlayRegistry.set(id, { layer, name });
  ensureOpacityPanel().appendChild(createOpacityRow(name, id, initialOpacity));
}

// 初期化時に一度だけイベント配線
document.addEventListener('DOMContentLoaded', wireOpacityUIOnce);

// ===== Minimal Opacity UI – auto-inject & wire =====

// 1) パネルを自動生成（HTMLに無くてもOK）
function ensureOpacityPanel() {
  let listEl = document.getElementById('layerList');
  if (listEl) return listEl;

  // asideパネルを作る
  const aside = document.createElement('aside');
  aside.id = 'layer-panel';
  aside.setAttribute('aria-label', 'Overlay opacity panel');
  aside.innerHTML = `<h2>Layers</h2><ul id="layerList" aria-live="polite"></ul>`;
  document.body.appendChild(aside);

  // 最小CSS（見えない問題を防ぐためインラインで注入）
  if (!document.getElementById('opacity-inline-style')) {
    const style = document.createElement('style');
    style.id = 'opacity-inline-style';
    style.textContent = `
      #layer-panel{position:absolute;z-index:1000;top:10px;left:10px;
        background:rgba(255,255,255,.95);border-radius:8px;padding:8px 10px;
        box-shadow:0 2px 8px rgba(0,0,0,.15);font:14px/1.35 system-ui,-apple-system,Segoe UI,sans-serif;min-width:220px}
      #layer-panel h2{margin:0 0 6px;font-size:14px;font-weight:600}
      #layerList{margin:0;padding:0;list-style:none;max-height:40vh;overflow:auto}
      .layer-row{display:grid;grid-template-columns:1fr 120px;gap:8px;align-items:center;padding:6px 0}
      .layer-name{white-space:nowrap;overflow:hidden;text-overflow:ellipsis}
      .opacity-slider{width:100%}
    `;
    document.head.appendChild(style);
  }
  return aside.querySelector('#layerList');
}

// 5) イベント配線（1回だけ）
function wireOpacityUIOnceMinimal() {
  if (wireOpacityUIOnceMinimal._done) return; wireOpacityUIOnceMinimal._done = true;
  const listEl = ensureOpacityPanel();
  listEl.addEventListener('input', (ev) => {
    const t = ev.target;
    if (!(t instanceof HTMLInputElement) || t.type !== 'range') return;
    const rec = overlayRegistry.get(Number(t.dataset.layerId));
    if (!rec) return;
    const v = (t.max === '100') ? Number(t.value) / 100 : Number(t.value);
    applyOpacity(rec.layer, v);
  }, { passive: true });
}

// 6) レイヤ登録API（レイヤ追加時に呼ぶ）
export function registerOverlayMinimal(layer, name = 'Overlay', initialOpacity = 1) {
  wireOpacityUIOnceMinimal(); // パネルが無ければここで作る
  const id = L.Util.stamp(layer);
  overlayRegistry.set(id, { layer, name });
  ensureOpacityPanel().appendChild(createOpacityRow(name, id, initialOpacity));
}

// 7) 互換用：旧コードが呼ぶ onLayerLoaded を吸収
export async function onLayerLoaded(arg, maybeName, maybeOpacity) {
  // 1) 正規の Leaflet レイヤーならそのまま登録
  const layer0 = coerceLeafletLayer(arg);
  if (layer0) {
    const name0 = maybeName ?? ((arg && arg.name) || 'Overlay');
    const init0 = (typeof maybeOpacity === 'number')
      ? maybeOpacity
      : (arg && typeof arg.opacity === 'number' ? arg.opacity : 1);
    try {
      const m = getLeafletMap();
      if (layer0.addTo && !layer0._map && m) layer0.addTo(m);
    } catch {}
    registerOverlay(layer0, name0, init0);
    return;
  }
  // 2a) File オブジェクトを直受けた場合
  if (typeof File !== 'undefined' && arg instanceof File) {
    const f = arg;
    const lower = f.name.toLowerCase();
    try {
      if (lower.endsWith('.zip')) {
        const lyr = await shapefileZipToLeafletLayer(f);
        const m = getLeafletMap(); if (m) lyr.addTo(m);
        registerOverlay(lyr, maybeName ?? trimExt(f.name), 1);
        return;
      }
      if (lower.endsWith('.geojson') || lower.endsWith('.json')) {
        const text = await f.text();
        const geo = JSON.parse(text);
        const m = getLeafletMap();
        const layer = L.geoJSON(geo, {
          pane: 'overlayPane', // ※paneは現状維持（必要なら後でvecPaneへ）
          renderer: (m?.options?.renderer && m.options.renderer instanceof L.Canvas)
            ? m.options.renderer
            : L.canvas({ padding: 0.5 }),
          interactive: true,
          bubblingMouseEvents: true,
          style: () => ({ opacity: 1, fillOpacity: 0.5 })
        });
        // 念のため親レイヤ1本で受ける
        layer.on('click', e => {
          const props = e.layer?.feature?.properties || {};
          if (m && e.latlng) L.popup().setLatLng(e.latlng)
            .setContent(`<pre style="margin:0;font:12px ui-monospace">${JSON.stringify(props, null, 2)}</pre>`)
            .openOn(m);
        });
        const map = getLeafletMap();
        if (map) { layer.addTo(map); layer.bringToFront(); }
        registerOverlay(layer, maybeName ?? trimExt(f.name), 1);
        return;
      }
      if (lower.endsWith('.tif') || lower.endsWith('.tiff')) {
        const lyr = await geotiffFileToLeafletLayer(f);
        const m = getLeafletMap(); if (m) lyr.addTo(m);
        registerOverlay(lyr, maybeName ?? trimExt(f.name), 1);
        return;
      }
      console.warn('[opacity] onLayerLoaded: unsupported file type (File)', f.name);
    } catch (e) {
      console.error('[opacity] onLayerLoaded: file handling failed', f.name, e);
    }
    return;
  }
  // 2b) 文字列（ファイル名）が来た場合は、対応フォーマットを自動ロード
  if (typeof arg === 'string') {
    const f = recentFiles.get(arg);
    if (!f) { console.warn('[opacity] onLayerLoaded: file not found in recentFiles', arg); return; }
    const lower = arg.toLowerCase();
    try {
      if (lower.endsWith('.zip')) {
        const lyr = await shapefileZipToLeafletLayer(f);
        const m = getLeafletMap();
        if (m) lyr.addTo(m);
        registerOverlay(lyr, maybeName ?? trimExt(f.name), 1);
        return;
      }
      if (lower.endsWith('.geojson') || lower.endsWith('.json')) {
        const text = await f.text();
        const geo = JSON.parse(text);
        const m = getLeafletMap();
        const layer = L.geoJSON(geo, {
          pane: 'overlayPane', // ※paneは現状維持（必要なら後でvecPaneへ）
          renderer: (m?.options?.renderer && m.options.renderer instanceof L.Canvas)
            ? m.options.renderer
            : L.canvas({ padding: 0.5 }),
          interactive: true, bubblingMouseEvents: true,
          style: () => ({ opacity: 1, fillOpacity: 0.5 })
        });
        layer.on('click', e => {
          const props = e.layer?.feature?.properties || {};
          if (m && e.latlng) L.popup().setLatLng(e.latlng)
            .setContent(`<pre style="margin:0;font:12px ui-monospace">${JSON.stringify(props, null, 2)}</pre>`)
            .openOn(m);
        });
        const map = getLeafletMap();
        if (map) { layer.addTo(map); layer.bringToFront(); }
        registerOverlay(layer, maybeName ?? trimExt(f.name), 1);
        return;
      }
      if (lower.endsWith('.tif') || lower.endsWith('.tiff')) {
        const lyr = await geotiffFileToLeafletLayer(f);
        const m = getLeafletMap();
        if (m) lyr.addTo(m);
        registerOverlay(lyr, maybeName ?? trimExt(f.name), 1);
        return;
      }
      // 他形式はここでスキップ（必要になったら追加）
      console.warn('[opacity] onLayerLoaded: unsupported file type', arg);
    } catch (e) {
      console.error('[opacity] onLayerLoaded: failed to handle file', arg, e);
      // 画面に短い注意を出す（任意）
      try {
        const list = ensureOpacityPanel();
        const li = document.createElement('li');
        li.className = 'layer-row';
        li.innerHTML = `<span class="layer-name">Shapefile読込失敗（shpjsが見つかりません）</span>`;
        list.appendChild(li);
        setTimeout(() => li.remove(), 5000);
      } catch {}
    }
    return;
  }
  console.warn('[opacity] onLayerLoaded: non-layer ignored', arg);
}

// ---- Leaflet Map resolver（DOMの window.map 汚染を回避）----
function getLeafletMap() {
  const m = globalThis.__leaflet_map__;
  if (m && typeof m.addLayer === 'function') return m;
  // 最後の保険：window.map が“たまたま”本物なら使う（通常は DOM 要素なので弾かれる）
  const wm = globalThis.map;
  if (wm && typeof wm.addLayer === 'function') return wm;
  return null;
}

// ---- GeoTIFF deps loader（バンドルから動的import。外部CDN/公開ファイルは不使用）----
let _geotiffDeps;
async function getGeoTiffDeps() {
  if (_geotiffDeps) return _geotiffDeps;
  const [georaster, GeoRasterLayerMod, proj4mod] = await Promise.all([
    import('georaster').then(m => m.default ?? m),
    import('georaster-layer-for-leaflet').then(m => m.default ?? m),
    import('proj4').then(m => m.default ?? m)
  ]);
  // georaster-layer-for-leaflet が globalThis.proj4 を参照するケースに備えて
  if (!globalThis.proj4) globalThis.proj4 = proj4mod;
  return (_geotiffDeps = { parseGeoraster: georaster, GeoRasterLayer: GeoRasterLayerMod, proj4: proj4mod });
}

// ---- File -> Leaflet Layer（GeoTIFF）----
async function geotiffFileToLeafletLayer(file, opts = {}) {
  const { parseGeoraster, GeoRasterLayer } = await getGeoTiffDeps();
  
  // -- 追加: UserDefined(32767) を WebMercator として救済するヘルパー --
  function normalizeProjectionForWebMercator(geo) {
    try {
      const code =
        (typeof geo.projection === 'number') ? geo.projection :
        (geo.projection && (geo.projection.code || geo.projection.epsg)) || null;
      // bbox がメートル値（~1.5e7, ~5.2e6）で 3857 と整合しているため、未定義は 3857 に寄せる
      if (code === 32767 || !geo.projection) {
        geo.projection = 'EPSG:3857';
      }
    } catch (_) {}
    return geo;
  }
  
  const ab = await file.arrayBuffer();
  let georaster = await parseGeoraster(ab); // 複数バンドにも対応
  if (Array.isArray(georaster)) georaster = georaster[0];
  normalizeProjectionForWebMercator(georaster); // ★追加：投影の明示
  
  // 透過スライダーが効くように opacity は Layer 側で受け持つ or DOM 直指定の両対応
  const layer = new GeoRasterLayer({
    georasters: [georaster],
    // ★高速化（最小・効果大）
    resolution: 256,                 // 低ズーム時の再サンプル密度を抑える
    useWebWorkers: true,             // geotiff.js をワーカーで並列化
    workerURL: 'https://unpkg.com/geotiff/dist/geotiff.worker.min.js',
    pixelValuesToColorFn: opts.pixelValuesToColorFn, // 必要なら後で差し替え
    opacity: 1
  });
  return layer;
}

// --- shpjs をバンドルから動的に読み込む（外部公開/CDNは使わない） ---
let _shpPromise = null;
function getShp() {
  return (_shpPromise ??= import('shpjs').then(m => m.default ?? m));
}

// ------------------------------------------------------------
// A) 直近にユーザーが選んだ File を名前で引けるようにキャプチャ
// ------------------------------------------------------------
const recentFiles = (globalThis.recentFiles ??= new Map()); // name -> File
document.addEventListener('change', (ev) => {
  const t = ev.target;
  if (t && t instanceof HTMLInputElement && t.type === 'file' && t.files) {
    for (const f of t.files) recentFiles.set(f.name, f);
  }
}, { capture: true });

function trimExt(name='') {
  const i = name.lastIndexOf('.');
  return i > 0 ? name.slice(0, i) : name;
}

async function shapefileZipToLeafletLayer(file /* File */) {
  const shp = await getShp();
  const ab = await file.arrayBuffer();
  const geo = await shp(ab); // FeatureCollection か {layerName: FeatureCollection} が返る
  let fc;
  if (geo && geo.type === 'FeatureCollection') {
    fc = geo;
  } else if (geo && typeof geo === 'object') {
    // 複数レイヤ（オブジェクト）→ まとめて1つに
    const feats = [];
    for (const k of Object.keys(geo)) {
      const g = geo[k];
      if (g && g.type === 'FeatureCollection' && Array.isArray(g.features)) feats.push(...g.features);
    }
    fc = { type: 'FeatureCollection', features: feats };
  }
  if (!fc) throw new Error('Invalid Shapefile content');
  // ここでも map の renderer を強制
  const m = getLeafletMap();
  const layer = L.geoJSON(fc, {
    pane: 'overlayPane', // ※paneは現状維持（必要なら後でvecPaneへ）
    renderer: (m?.options?.renderer && m.options.renderer instanceof L.Canvas)
      ? m.options.renderer
      : L.canvas({ padding: 0.5 }),
    interactive: true, bubblingMouseEvents: true,
    style: () => ({ opacity: 1, fillOpacity: 0.5 })
  });
  layer.on('click', e => {
    const props = e.layer?.feature?.properties || {};
    if (m && e.latlng) L.popup().setLatLng(e.latlng)
      .setContent(`<pre style="margin:0;font:12px ui-monospace">${JSON.stringify(props, null, 2)}</pre>`)
      .openOn(m);
  });
  return layer;
}

// DOM準備後にUI配線（保険）
document.addEventListener('DOMContentLoaded', wireOpacityUIOnceMinimal);