/**
 * JobController - Handles job execution with cancellation support
 */
export class JobController {
  constructor(name) { 
    this.name = name; 
    this._current = null; 
  }
  
  cancel() { 
    if (this._current?.ac) this._current.ac.abort(); 
  }
  
  async run(label, fn) {
    // 旧処理を中断
    this.cancel();
    const ac = new AbortController();
    const token = Date.now().toString(36) + Math.random().toString(36).slice(2);
    this._current = { ac, token, label };
    try {
      const res = await fn(ac.signal, token);
      if (ac.signal.aborted) throw new DOMException('Aborted', 'AbortError');
      return res;
    } catch (e) {
      if (e?.name === 'AbortError') {
        console.warn({ phase:'job', label, msg:'aborted' });
        return;
      }
      throw e;
    } finally {
      // 自分が現役なら解放
      if (this._current?.token === token) this._current = null;
    }
  }
}