import L from 'leaflet';
// 初回は読み込まず、ユーザーが「描画開始」操作をした瞬間に読み込む
async function ensureLeafletDraw() {
  if (L.Draw) return;
  await import('leaflet-draw');
}
// 例：描画ボタンのクリック時に ensureLeafletDraw() を呼ぶ
import './style.css';
import { report, step, DEBUG, attachDebugTrayIfRequested, raise, ERR } from './errors.js';
import { renderDiagnostics } from './diagnostics.js';
import { JobController } from './job-controller.js';
import { registerOverlay, registerOverlayMinimal, onLayerLoaded } from './opacity-control.js';
import { wireP1HandlersOnce } from './file-handlers.js';
import { initMap } from './map-init.js';
import { showBusy, hideBusy, $, $$, save, load, initUIControls, initCollapseButton, initDrop, simulateProgress, initPresets, initExportButtons, initKeyboardShortcuts, ensureFileBar } from './ui-utils.js';
// Add the import for the locate control
import { installLocateControl } from './locate-control.js';

// ---- 追加: 旧コードが参照している可能性のある ho() を安全化（無ければ定義、あっても関数でなければ置き換え）
// ho(...) is not a function を潰すための最小ポリフィル（関数ならそのまま、未定義/非関数なら no-op ラッパ）
try {
  if (typeof window !== 'undefined') {
    const prev = window.ho;
    if (typeof prev !== 'function') {
      window.ho = (fn) => { if (typeof fn === 'function') { try { fn(); } catch(e) { /* swallow */ } } };
    }
  }
} catch {}

// ロガー強制有効化＋起動確認ログ
DEBUG.on = true;
attachDebugTrayIfRequested();    // URLに ?debug=1 を付けると画面左下に簡易トレイ
report('App started');           // ← これが出なければ import されていません
window.report = report;          // Consoleから手動で呼べるように（任意）
// Diagnostics
window.addEventListener('load', renderDiagnostics, { once: true });

// DOM 準備後に1回だけ配線
window.addEventListener('DOMContentLoaded', wireP1HandlersOnce, { once: true });
window.addEventListener('load', () => {
  // Initialize the map（必ず L.Map を取得する安全版）
  const mapCandidate = initMap();
  const map =
    (mapCandidate instanceof L.Map && mapCandidate) ||
    (window.__leaflet_map__ instanceof L.Map && window.__leaflet_map__) ||
    (Object.values(window).find(v => v instanceof L.Map) || null);
  if (!map) throw new Error('Map init failed: L.Map instance not found after initMap()');
  
  // === OSM + 地理院（写真）／レイヤ切替：生成→ポータル→固定→色・位置（背景は標準） ===
  // 1) ベース
  const osm = L.tileLayer('https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png', {
    maxZoom: 19, attribution: '&copy; OpenStreetMap contributors'
  }).addTo(map);
  const gsi = L.tileLayer('https://cyberjapandata.gsi.go.jp/xyz/seamlessphoto/{z}/{x}/{y}.jpg', {
    maxZoom: 18,
    attribution: '<a href="https://maps.gsi.go.jp/development/ichiran.html#seamlessphoto" target="_blank" rel="noopener">地理院タイル（写真）</a> | © 国土地理院'
  });

  // 2) レイヤ切替（右下）
  const layersCtrl = L.control.layers(
    { 'OSM 標準': osm, '地理院タイル（航空写真）': gsi },
    null,
    { collapsed: false, position: 'bottomright' }
  ).addTo(map);
  layersCtrl.expand?.();

  // ▼ 追加: 縮尺バー（左下・常時）
  L.control.scale({ position:'bottomleft', imperial:false, updateWhenIdle:true }).addTo(map);

  // 3) ポータル固定関数（背景は標準のまま）
  function portalLayersControl(ctrl, { gap=12, z=50000, outline='1px solid rgba(11,27,63,0.28)', deltaRight=0, deltaBottom=14 } = {}) {
    const el = ctrl._container;
    if (!el.dataset.__portaled) {
      el.__origParent = el.parentNode;
      el.__origNext   = el.nextSibling;
      el.__ph         = document.createComment('layers-portal-placeholder');
      el.__origParent.insertBefore(el.__ph, el.__origNext);
      document.body.appendChild(el);
      el.dataset.__portaled = '1';
    }
    const mapEl = ctrl._map.getContainer();
    const reposition = () => {
      const r = mapEl.getBoundingClientRect();
      const rightPx  = Math.max(gap, Math.round(window.innerWidth  - r.right + gap)  + deltaRight);
      const bottomPx = Math.max(gap, Math.round(window.innerHeight - r.bottom + gap) + deltaBottom);
      Object.assign(el.style, {
        position: 'fixed',
        right: rightPx + 'px',
        bottom: bottomPx + 'px',
        zIndex: String(z),
        pointerEvents: 'auto',
        outline,
        borderRadius: '10px'
        // 背景は設定しない → Leaflet標準を維持
      });
      el.style.background = ''; // 念のため透明指定を解除
    };
    reposition();
    window.addEventListener('resize', reposition);
    window.__layers_portal_reposition = reposition;
  }
  portalLayersControl(layersCtrl, { outline:'1px solid rgba(11,27,63,0.28)', deltaRight:0, deltaBottom:14 });
  
  // Install the locate control
  try { 
    installLocateControl(map); 
  } catch (e) { 
    console.warn('[locate] install failed', e); 
  }
  
  // Initialize UI controls
  initUIControls();
  // UIのみ：右カラムに Layers/透明度 のホストを常設し、左上への迷い込みを防ぐ
  try {
    ensureLayersHost();
    armLayersGuard();
  } catch (e) {
    console.warn('[layers:guard]', e);
  }
  
  // Initialize collapse button
  initCollapseButton();
  
  // Initialize export buttons
  initExportButtons();
  
  // Initialize keyboard shortcuts
  initKeyboardShortcuts();
  
  // Initialize file bar
  document.addEventListener('DOMContentLoaded', ensureFileBar);
  
  console.log('森林管理ツールが起動しました');

  // オフラインモードでの警告を表示
  const isOffline = typeof __VITE_IS_OFFLINE__ !== 'undefined' ? __VITE_IS_OFFLINE__ : false;
  if (isOffline) {
    console.log('オフラインモードで実行中');
  }

  // ローカルストレージクリア機能
  const clearStorageButton = document.getElementById('btnClearLocal');
  if (clearStorageButton) {
    clearStorageButton.addEventListener('click', () => {
      if (confirm('ローカルに保存されたすべてのデータを削除しますか？')) {
        try {
          localStorage.clear();
          // 設定マネージャーの設定もクリア
          // settingsManager.clearSettings();
          // ページをリロードして初期状態に戻す
          location.reload();
          console.log('ローカルストレージをクリアしました');
        } catch (error) {
          console.error('ローカルストレージのクリアに失敗しました:', error);
          alert('データのクリアに失敗しました: ' + error.message);
        }
      }
    });
  }

  // ドラッグ＆ドロップ機能の要素を取得
  const dropArea = document.getElementById('drop-area');

  // ドラッグ＆ドロップイベントの設定
  dropArea?.addEventListener('dragover', (e) => {
    e.preventDefault();
    dropArea.classList.add('dragover');
  });

  dropArea?.addEventListener('dragleave', () => {
    dropArea.classList.remove('dragover');
  });

  dropArea?.addEventListener('drop', (e) => {
    e.preventDefault();
    dropArea.classList.remove('dragover');
    
    const files = e.dataTransfer.files;
    handleDroppedFiles(files);
  });

  // ===== GeoTIFF 読み込み =====
  const fileInput = document.getElementById('geotiffInput');
  const shpInput  = document.getElementById('shpZipInput');
  const rasterOpacityEl   = document.getElementById('opacityRaster');
  const vectorOpacityEl   = document.getElementById('opacityVector');
  const rasterOpacityVal  = document.getElementById('outRaster');
  const vectorOpacityVal  = document.getElementById('outVector');

  let currentRasterLayer = null;
  let currentVectorLayer = null;

  // 起動時：重複UI/不要UIを除去（HTML残骸があっても安全）
  document.getElementById('floating-uploader')?.remove();
  ['btnAreaSelect','btnAreaClear','btnAddAdmin'].forEach(id=>document.getElementById(id)?.remove());

  function getRasterOpacity(){ return Number(rasterOpacityEl?.value ?? 0.75); }
  function getVectorOpacity(){ return Number(vectorOpacityEl?.value ?? 0.50); }
  function vectorStyle(op) {
    return {
      color: '#0B7285',
      weight: 1.2,
      opacity: Math.max(0, Math.min(1, op)),        // 線
      fillColor: '#4DABF7',
      fillOpacity: Math.max(0, Math.min(1, op*0.7)) // 塗りは少し薄め
    };
  }
  function applyRasterOpacity(){ if (currentRasterLayer) currentRasterLayer.setOpacity(getRasterOpacity()); }
  function applyVectorOpacity(){ if (currentVectorLayer) currentVectorLayer.setStyle(vectorStyle(getVectorOpacity())); }

  // スライダー：値表示＆即時反映
  rasterOpacityEl?.addEventListener('input', ()=>{
    if (rasterOpacityVal) rasterOpacityVal.textContent = (+rasterOpacityEl.value).toFixed(2);
    applyRasterOpacity();
  });
  vectorOpacityEl?.addEventListener('input', ()=>{
    if (vectorOpacityVal) vectorOpacityVal.textContent = (+vctorOpacityEl.value).toFixed(2);
    applyVectorOpacity();
  });

  // ▼ 変更: ここではスピナーを触らない（実処理側で制御）
  fileInput?.addEventListener('change', async (ev) => {
    const f = ev.target.files?.[0]; if (!f) return;
    try {
      await step(`File selected: ${f.name} (${f.size} bytes)`, async () => {/* 実処理は file-handlers.js */});
    } catch (e) {
      report(raise(ERR.GEO_PARSE, 'GeoTIFF 読み込みに失敗', { file: f?.name }, e));
    }
  });

  // ===== Shapefile(ZIP) 読み込み =====
  // ▼ 変更: スピナーは file-handlers.js 側で制御
  shpInput?.addEventListener('change', async (ev) => {
    const f = ev.target.files?.[0]; if (!f) return;
    try {
      await step(`Vector selected: ${f.name} (${f.size} bytes)`, async () => {/* 実処理は file-handlers.js */});
    } catch (e) {
      report(raise(ERR.GEO_PARSE, 'Shapefile 読み込みに失敗', { file: f?.name }, e));
    }
  });

  /**
   * ドロップされたファイルを処理
   * @param {FileList} files - ファイルリスト
   */
  async function handleDroppedFiles(files) {
    for (const file of files) {
      if (file) {
        try {
          // GeoTIFFファイルの場合は段階ログ付き処理を使用
          if (file.name.toLowerCase().endsWith('.tif') || file.name.toLowerCase().endsWith('.tiff') || file.name.toLowerCase().endsWith('.geotiff')) {
            // Note: This functionality is now handled by the file handlers module
            console.log('GeoTIFFファイルがドロップされました:', file.name);
          } else {
            await step(`File selected: ${file.name} (${file.size} bytes)`, async () => {
              console.log('ファイルデータ読み込み完了:', file.name);
              
              // ファイルタイプに応じた処理
              switch (file.name.split('.').pop().toLowerCase()) {
                case 'geotiff':
                case 'tif':
                case 'tiff':
                  // handleGeoTIFFFile(file, fileData);
                  break;
                case 'gpkg':
                  // handleGeoPackageFile(file, fileData);
                  break;
                case 'shp':
                case 'zip':
                  // handleShapefileFile(file, fileData);
                  break;
                default:
                  console.warn('未知のファイルタイプ:', file.name);
              }
            });
          }
        } catch (error) {
          console.error('ファイル読み込みエラー:', error);
          report(error, 'error');
          alert('ファイルの読み込みに失敗しました: ' + error.message);
        }
      }
    }
  }

  // ドラッグ開始イベント
  function handleDragStart(e) {
    draggedLayerId = e.target.dataset.id;
    e.target.classList.add('dragging');
    e.dataTransfer.effectAllowed = 'move';
    e.dataTransfer.setData('text/html', e.target.outerHTML);
  }

  // ドラッグオーバーイベント
  function handleDragOver(e) {
    e.preventDefault();
    e.dataTransfer.dropEffect = 'move';
    return false;
  }

  // ドラッグエンターイベント
  function handleDragEnter(e) {
    e.preventDefault();
    e.target.classList.add('drag-over');
  }

  // ドラッグリーブイベント
  function handleDragLeave(e) {
    e.target.classList.remove('drag-over');
  }

  // ドロップイベント
  function handleDrop(e) {
    e.stopPropagation();
    e.preventDefault();
    
    if (draggedLayerId) {
      const targetElement = e.target.closest('.layer-item');
      if (targetElement && targetElement.dataset.id !== draggedLayerId) {
        const draggedIndex = layerList.findIndex(layer => layer.id === draggedLayerId);
        const targetIndex = layerList.findIndex(layer => layer.id === targetElement.dataset.id);
        
        if (draggedIndex !== -1 && targetIndex !== -1) {
          // レイヤーリストの順序を変更
          const [draggedLayer] = layerList.splice(draggedIndex, 1);
          layerList.splice(targetIndex, 0, draggedLayer);
          
          // レイヤーツリーを更新
          updateLayerTree();
          
          // 設定を保存
          // settingsManager.setSetting('layerList', layerList);
        }
      }
    }
    
    return false;
  }

  // ドラッグ終了イベント
  function handleDragEnd(e) {
    e.target.classList.remove('dragging');
    document.querySelectorAll('.layer-item').forEach(item => {
      item.classList.remove('drag-over');
    });
    draggedLayerId = null;
  }

  // レイヤーの表示/非表示を切り替え
  function toggleLayerVisibility(layerId, visible) {
    // const lyr = mapManager.getLayerByUiId(layerId);
    // if (!lyr) return;
    // const m = mapManager.map;
    // if (visible) { if (!m.hasLayer(lyr)) lyr.addTo(m); }
    // else { if (m.hasLayer(lyr)) m.removeLayer(lyr); }
  }

  // レイヤーの透過度を更新
  function updateLayerOpacity(layerId, opacity) {
    // mapManager.setOpacityByUiId(layerId, opacity);
  }

  // レイヤーを削除
  function removeLayer(layerId) {
    layerList = layerList.filter(layer => layer.id !== layerId);
    updateLayerTree();
    // 実際の実装では、地図上のレイヤーも削除する
    console.log(`レイヤー ${layerId} を削除しました`);
    
    // 設定を保存
    // settingsManager.setSetting('layerList', layerList);
  }

  // ===== ドロップゾーン =====
  initDrop('dropGeo', '#geotiffInput', files=>{
    // TODO: 既存のGeoTIFF読込関数に接続 (例) loadGeoTiff(files[0], onProgress)
    simulateProgress('dropGeo', ()=> onLayerLoaded(files[0].name, {type:'raster'}));
  });
  initDrop('dropShp', '#shpZipInput', files=>{
    // TODO: 既存のShapefile読込関数に接続 (例) loadShapefileZip(files[0], onProgress)
    simulateProgress('dropShp', ()=> onLayerLoaded(files[0].name, {type:'vector'}));
  });
});

// UI: 左上の #layer-panel を「レイヤ読み込み」と「メンテナンス」の間へ移設（機能変更なし）
function relocateLayerPanelStrict() {
  const root  = document.querySelector('#app') || document.body;
  const panel = document.querySelector('#layer-panel') ||
                Array.from(root.querySelectorAll('aside,div,section')).find(n => /^\s*Layers\s*$/i.test(n.textContent||''));
  if (!panel) return;
  const nodes = Array.from(root.querySelectorAll('h1,h2,h3,h4,summary,button,label,div,section,details,li,p'));
  const findByText = (re) => nodes.find(n => re.test(n.textContent||''));
  const maintH = findByText(/メンテナンス/);
  if (!maintH) return;
  // 見出し直前に差し込み＝両セクションの“間”
  Object.assign(panel.style, {
    position:'static', top:'', left:'', right:'', bottom:'', zIndex:'', margin:'0 0 12px 0', maxWidth:'100%'
  });
  maintH.before(panel);
}

// （既存）地図や右ペインの構築完了後に呼ぶ
document.addEventListener('DOMContentLoaded', () => {
  // …既存初期化…
  relocateLayerPanelStrict();
});